import React, { useState, useEffect} from 'react'
import './Playlist.css'
import axios from 'axios'

export function Playlist(){

    const [playlists, setPlaylists] = useState([])

	useEffect(() => {
		const fetchPlaylists = async () => {
		try {
			const res = await axios.get("http://localhost:8800/playlist")
			setPlaylists(res.data)
		} catch(err) {
			console.log(err)
		}}
		fetchPlaylists()
		}, []);

    return (
        <div className="AppPlaylist">
			<div id="display">
				{playlists.map(playlist=> (
				<div className="playlist" key={playlist.id}>
					<div>
						<a href={playlist.playlistLink}>
						<img className="picsPlaylist" id={playlist.id} alt={playlist.playlistName} src={playlist.imageLink}></img></a>
						<p id="name">{playlist.playlistName}</p>
					</div>
				</div>
				))}
			</div>
		</div>

    )
}

export default Playlist