import React, { useState, useEffect } from 'react'
import axios from 'axios'
import './Others.css'

const Others =() => {

    const [sugg, setSugg] = useState({
      artist: "",
      track: "",
    })

    const [isDisabled, setIsDisabled] = useState(false)

    const handleChange = (e) => {
      setSugg((prev) => ({...prev, [e.target.name]: e.target.value}))
      if (e.target.value.trim().length < 1) {
        setIsDisabled(true)
      } else {
        setIsDisabled(false)
      }
    }

    const handleClick = async e => {
      e.preventDefault()
      try {
        await axios.post("http://localhost:8800/suggestions", sugg).then(res => console.log(res.data))
        window.location.reload()
      } catch(err) {
        console.log(err)
      }
    }

    const [suggestions, setSuggestions] = useState([])

	useEffect(() => {
		const fetchSuggestions = async () => {
		try {
			const res = await axios.get("http://localhost:8800/suggestions")
			setSuggestions(res.data)
		} catch(err) {
			console.log(err)
		}}
		fetchSuggestions()
		}, []);

      return (
        <div className='AppOthers'>
            <div id="preface">
                <p id="pre">here's a space for you to share your song recommendations with others 😊</p>
            </div>
            <div id="formContainer">
                <p id="reccoSong">recommend here 🎵</p>
                <hr id="mainLine"></hr>
                <form id="form">
                    <div id="artist">
                        <label id="artistLabel">Artist</label>
                        <input type="text" name="artist" onChange={handleChange}></input>
                    </div>

                    <div id="track">
                        <label id="trackLabel">Track</label>
                        <input type="text" name="track" onChange={handleChange}></input>
                    </div>

                    <div id="submit">
                        <button type="submit" disabled={isDisabled} id="submitBtn" onClick={handleClick}>Suggest!</button>
                    </div>
                </form>
            </div>

            <div id="displayContainer">
                <p id="reccoHeader">everyone's recommendations 📌</p>
                <hr id="mainLine"></hr>
                <table>
                    <tr id="tableHeader">
                        <th>no.</th>
                        <th>artist</th>
                        <th>track</th>
                    </tr>

                    {suggestions.map((suggestion) => {
                        return(
                        <tr id="row" key={suggestion.id}>
                            <td id="id">{suggestion.id}</td>
                            <td><div id="artist">{suggestion.artist}</div></td>
                            <td><div id="track">{suggestion.track}</div></td>
                        </tr>)
                    })}

                </table>

            </div>

        </div>
      )
  }

export default Others