import React, { useState, useEffect} from 'react'
import './Artist.css'
import axios from 'axios'
import { Link } from 'react-router-dom'

const Artist = (props) => {

	const [artists, setArtists] = useState([])

	useEffect(() => {
		const fetchArtists = async () => {
		try {
			const res = await axios.get("http://localhost:8800/artist")
			setArtists(res.data)
		} catch(err) {
			console.log(err)
		}}
		fetchArtists()
		}, []);


	return (
		<div className="AppArtist">
			<div id="display">
				{artists.map(artist=> (
				<div className="artist" key={artist.id}>
					<div id="box">
						<button className="nav"><Link to={'/Layout'} state={{id: artist.id, name: artist.artistName, album: artist.album, trackOne: artist.trackOne,
							trackTwo: artist.trackTwo, trackThree:artist.trackThree}}>
						<img className="picsArtist" id={artist.id} alt={artist.artistName} src={artist.imageLink}></img></Link></button>
						<p id="name">{artist.artistName}</p>

					</div>
				</div>
				))}
			</div>
		</div>
	)
}

export default Artist