import React from 'react'
import {useLocation} from 'react-router-dom'
import './Layout.css'

const Layout = (props) => {

	const location = useLocation()
	const data = location.state
	console.log(data)

	return (
		<div className='main'>
			<div id="header">
				<h2>{data.name}</h2>
			</div>

			<div id="album">
				<p>best album</p>
				<iframe id="alb" src={data.album} width="100%" frameBorder="0" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe>
			</div>

			<div id="tracks">
				<p>top tracks</p>
				<div id="tracksBox">
				<iframe id="song" src={data.trackOne} width="100%" frameBorder="0" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe>
				<iframe id="song" src={data.trackTwo} width="100%" frameBorder="0" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe>
				<iframe id="song" src={data.trackThree} width="100%" frameBorder="0" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture" loading="lazy"></iframe></div>
			</div>
		</div>
		)
}


export default Layout

